import ProjectsArticles from '~/components/projectsArticles.vue'
import Theheader from '~/components/Theheader.vue'
export default {
  components: { Theheader, ProjectsArticles },
  async asyncData ({ $content, params }) {
    const projects = await $content('projects', params.slug).fetch()

    return {
      projects
    }
  },
  data () {
    return {
      query: '',
      result: [],
      nosearch: true
    }
  },
  head () {
    const title = 'NuxtLover | Projects'
    const description = 'Awesome projects list build by nuxters'
    return {
      title,
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: description
        },
        // Open Graph
        {
          hid: 'og:title',
          property: 'og:title',
          content: title
        },
        {
          hid: 'og:description',
          property: 'og:description',
          content: description
        },
        // Twitter Card
        {
          hid: 'twitter:title',
          name: 'twitter:title',
          content: title
        },
        {
          hid: 'twitter:description',
          name: 'twitter:description',
          content: description
        }
      ]
    }
  },
  watch: {
    async query (query) {
      if (!query) {
        this.result = []
        return
      }

      this.result = await this.$content('projects')
        .only(['title', 'slug', 'description'])
        .sortBy('createdAt', 'asc')
        .limit(12)
        .search(query)
        .fetch()
    }
  },
  methods: {
    reset () {
      this.query = ''
    }
  }

}
